export default {
	data() {
		return {
			textList:['财源滚滚','步步高升','一夜暴富','每赌必赢','逢凶化吉','业绩长虹','生意兴隆','贵人相助'],
			selIdList:[]
		}
	},
	methods: {
		goMatch(){
			try {
				let wishList=[]
				if(this.selIdList.length>0){
					this.selIdList.forEach((item)=>{
						wishList.push(this.textList[item])
					})
				}
				console.log(wishList)
			    uni.setStorage({
			        key: 'wishList',
			        data: wishList,
			        success: function () {
			           uni.navigateTo({
			               url: '/pages/birthday/birthday'
			           }); 
			        }
			    });
			} catch (e) {
			    // error
			}
		},
		getUserInfo(info) {
			console.log(info);
			return
			if (info.detail.userInfo) {
			  console.log("点击了同意授权");
			  wx.login({
				success: (res)=>{
				  if (res.code) {
					wx.request({
					  url: 'https://app.movetechy.com/login/sign',
					  data: {
						code: res.code,
						rawData:info.detail.rawData
					  },
					  header: {
						'content-type': 'application/json' // 默认值
					  },
					  success: (result)=>{
						console.log(result)
						if(result.statusCode==200&&result.data.code==0){
							let token=result.data.token
							uni.setStorageSync('token',token);
							this.getInfo(token)
						}else{
							uni.showToast({
								title:result.data.msg,
								icon:"none",
								duration:600
							})
						}
						
					  }
					})
				  } else {
					console.log("授权失败");
				  }
				},
			  })
			} else {
			  console.log("点击了拒绝授权");
			  this.back()
			}
		},
		selItem(index){
			let i= this.selIdList.indexOf(index)
			if(i>-1){
				this.selIdList.splice(i,1)
			}else{
				if(this.selIdList.length>=3){
					uni.showToast({
					    title: '以上限求三项',
						icon:'none',
					    duration: 1000
					});
				}else{
					this.selIdList.push(index)
				}
			}
		}
	}
}
